(function () {
  // GLOBAL VARIABLES AND CACHED ELEMENTS
  let jQuery;
  let $navList;

  // TOP LEVEL TESTING FNS
  const runPoll = (pollCondition, offer, maxAttempts = 50) => {
    if (!maxAttempts) {
      return;
    }

    if (!pollCondition()) {
      maxAttempts -= 1; // eslint-disable-line no-param-reassign
      return setTimeout(runPoll, 100, pollCondition, offer, maxAttempts); // eslint-disable-line consistent-return, max-len
    }

    offer();
  };

  const pollCondition = () => {
    jQuery = window.jQuery;

    if (jQuery) {
      $navList = jQuery('.nav-list.nav-list--categories');
      return Boolean($navList.length);
    }

    return false;
  };

  // /////////////////////////
  // TEST SPECIFIC JAVASCRIPT
  // /////////////////////////

  const getMerchCategoryData = (categoryName) => {
    const siteUrl = window.UA.CONFIG.base_url + window.UA.REQUEST.runtime.locale_segment;

    switch (categoryName) {
      case 'Men':
        return {
          dataDtmKey: 'Men|Spotlight-GEAT-236|UA Rush',
          heading: 'UA RUSH™',
          imageSrc: 'https://underarmour.scene7.com/is/image/Underarmour/FW19_TopNav_MensTee_Rush?fmt=jpg&wid=300',
          shopUrl: siteUrl + '/mens/rush-infrared-technology-athletic-apparel/g/3943t',
        };
      case 'Women':
        return {
          dataDtmKey: 'Women|Spotlight-GEAT-236|UA ColdGear',
          heading: 'UA ColdGear®',
          imageSrc: 'https://underarmour.scene7.com/is/image/Underarmour/FW19_TopNav_WomensCG_CGReinvent?fmt=jpg&wid=300',
          shopUrl: siteUrl + '/womens/cold-gear/g/3c11q',
        };
      case 'Boys':
        return {
          dataDtmKey: 'Boys|Spotlight-GEAT-236|UA Hoodies',
          heading: 'UA Hoodies',
          imageSrc: 'https://underarmour.scene7.com/is/image/Underarmour/FW19_TopNav_BoysArmourFleece?fmt=jpg&wid=300',
          shopUrl: siteUrl + '/boys/hoodies/g/3f11',
        };
      case 'Girls':
        return {
          dataDtmKey: 'Girls|Spotlight-GEAT-236|UA Hoodies',
          heading: 'UA Hoodies',
          imageSrc: 'https://underarmour.scene7.com/is/image/Underarmour/FW19_TopNav_GirlsHoodie_SS20Youth?fmt=jpg&wid=300',
          shopUrl: siteUrl + '/girls/hoodies/g/3i11',
        };
      case 'Shoes':
        return {
          dataDtmKey: 'Shoes|Spotlight-GEAT-236|UA TriBase Reign 2',
          heading: 'UA TriBase™ Reign 2',
          imageSrc: 'https://underarmour.scene7.com/is/image/Underarmour/FW19_TopNav_Tribase?fmt=jpg&wid=300',
          shopUrl: siteUrl + '/training/g/3i4',
        };
      default:
        return console.error(`the category ${categoryName} was not found`); // eslint-disable-line no-console
    }
  };

  const getNewColumnHTML = ({ dataDtmKey, heading, imageSrc, shopUrl }) => `
      <div class="nav-column default GEAT-236-nav-column">
        <a
          data-dtm-key="${dataDtmKey}"
          href=${shopUrl}
        >
          <img
            alt="UA Product Image"
            class="GEAT-236-hero-image"
            src=${imageSrc}
          />
        </a>

        <div class="GEAT-236-top-heading">
          ${heading}
        </div>

        <a
          class="GEAT-236-shop-link"
          data-dtm-key="${dataDtmKey}"
          href=${shopUrl}
        >
          Shop Collection
        </a>
      </div>
    `;

  const getNewContainerHTML = ({ dataDtmKey, heading, imageSrc, shopUrl }) => `
      <div class="nav-column spotlight GEAT-236-spotlight-container">
        <a
          data-dtm-key="${dataDtmKey}"
          href=${shopUrl}
        >
          <img
            alt="UA Product Image"
            class="GEAT-236-hero-image"
            src=${imageSrc}
          />
        </a>

        <div class="GEAT-236-top-heading">
          ${heading}
        </div>

        <a
          class="GEAT-236-shop-link"
          data-dtm-key="${dataDtmKey}"
          href=${shopUrl}
        >
          Shop Collection
        </a>
      </div>
    `;

  const handleCategory = (category, isOnTablet) => {
    const $menu = jQuery(category).children('.ua-top-nav-menu');

    const categoryName = $menu
      .children('.ua-top-nav-item--mobile')
      .data('dtm-key');

    if (categoryName === 'Outlet') return;
    if (categoryName === 'Gift Guide') return;

    const container = $menu.children('.ua-top-nav-column-container');
    const data = getMerchCategoryData(categoryName);
    let html;

    if (isOnTablet) {
      html = getNewContainerHTML(data);
      container.addClass('spotlight-container');
    } else {
      html = getNewColumnHTML(data);
    }

    container.prepend(html);
  };

  // From John for hover tracking
  const attachHoverMutationObserver = () => {
    const targetNode = document.querySelector('.nav-list.nav-list--categories');
    const config = {
      attributes: true,
      childList: true,
      subtree: true
    };

    const callback = function (mutationsList, observer) {
      for (let mutation of mutationsList) {
        const isHover = Array.from(mutation.target.classList).indexOf('active-hover') > -1;

        if (isHover) {
          s.clearVars();
          s.tl(true,'o','navHover_GEAT-236');
        }
      }
    };

    const observer = new MutationObserver(callback);
    observer.observe(targetNode, config);
  }

  const offer = () => {
    jQuery(styles).appendTo('head');

    const categories = $navList
      .children('.ua-top-nav-category')
      .not('.hide-desktop');

    const isOnTablet = window.screen.width >= 768 && window.screen.width < 991;
    const isOnDesktop = window.screen.width >= 991;

    if (!isOnTablet && !isOnDesktop) return;

    categories.each((index, category) => handleCategory(category, isOnTablet));
  };

  runPoll(pollCondition, offer, 50);
  attachHoverMutationObserver();
}());
